@tailwind base;
  @tailwind components;
  @tailwind utilities;

.ant-upload-drag-container {
  padding: 50px 100px;
}

.root-container {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.site-page-header {
  border: 1px solid rgb(235, 237, 240);
  background-color: #f5f5f5;
  padding: 24px;
}

.content-container {
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  flex: 1;
  padding-bottom: 20px;
}

.ant-upload-drag{
  background: none !important;
  border: 0px !important;
}

.buttons-container {
  margin: 10px 0;
}

.buttons-container > button {
  margin: 0 5px;
  float:left;
}

.buttons-container > icon {
  margin: 0 5px;
}

@media (max-width: 400px) {
  .site-page-header {
    display: none;
  }
}

.cropper {
  margin: 10 0;
}

.container{
  margin-top: -20px;
  min-width: 450px;  
  background: #fff;
}
.container.disable .editor-panel,
.container.disable .controls .reset-filter,
.container.disable .controls .save-img{
  opacity: 0.5;
  pointer-events: none;
}
.container h2{
  margin-top: -8px;
  font-size: 22px;
  font-weight: 500;
}
.container .wrapper{
  display: flex;
  min-height: 335px;
}
.wrapper .editor-panel{
  padding: 15px 20px;
  min-width: 280px;
  border-radius: 5px;
  border: 1px solid #ccc;

}
.editor-panel .title{
  display: block;
  font-size: 16px;
  margin-bottom: 12px;
}
.editor-panel .options, .controls{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.editor-panel button{
  outline: none;
  height: 40px;
  font-size: 14px;
  color: #6C757D;
  background: #fff;
  border-radius: 3px;
  margin-bottom: 8px;
  border: 1px solid #aaa;
}
.editor-panel .filter button{
  width: calc(100% / 2 - 4px);
}
.editor-panel button:hover{
  background: #f5f5f5;
}
.filter button.active{
  color: #fff;
  border-color: #5372F0;
  background: #5372F0;
}
.filter .slider{
  margin-top: 12px;
}
.filter .slider .filter-info{
  display: flex;
  color: #464646;
  font-size: 14px;
  justify-content: space-between;
}

.filter .slider .filter-info .filter-info-saturation{
  display: flex;
  color: #464646;
  font-size: 14px;
  justify-content: space-between;
}

.filter .sliderSaturation .filter-info-saturation{
  display: flex;
  color: #464646;
  font-size: 14px;
  justify-content: space-between;
}

.editor-panel .rotate{
  margin-top: 17px;
}
.editor-panel .rotate button{
  display: flex;
  align-items: center;
  justify-content: center;
  width: calc(100% / 3 - 3px);
}
.rotate .options button:nth-child(3),
.rotate .options button:nth-child(4){
  font-size: 18px;
}
.rotate .options button:active{
  color: #fff;
  background: #5372F0;
  border-color: #5372F0;
}
.wrapper .preview-img{
  flex-grow: 1;
  display: flex;
  overflow: hidden;
  margin-left: 20px;
  border-radius: 5px;
  align-items: center;  
}
.preview-img img{

  width: 80%;
  height: 100%;
  object-fit: contain;
  border: 1px solid #ccc;
}
.controls button{
  padding: 11px 20px;
  font-size: 14px;
  border-radius: 3px;
  outline: none;
  color: #fff;
  cursor: pointer;
  background: none;
  transition: all 0.3s ease;
  text-transform: uppercase;
}
.controls .reset-filter{
  color: #6C757D;
  border: 1px solid #6C757D;
  width: 120px;
}
.controls .reset-filter:hover{
  color: #fff;
  background: #6C757D;
}
.controls .choose-img{
  background: #6C757D;
  border: 1px solid #6C757D;
}
.controls .save-img{
  margin-left: 5px;
  background: #5372F0;
  border: 1px solid #5372F0;
}

@media screen and (max-width: 760px) {
  .container{
    padding: 25px;
  }
  .container .wrapper{
    flex-wrap: wrap-reverse;
  }
  .wrapper .editor-panel{
    width: 80%;
  }
  .wrapper .preview-img{
    width: 100%;
    margin: 0 0 15px;
  }
}

@media screen and (max-width: 500px) {
  .controls button{
    width: 100%;
    margin-bottom: 10px;
  }
  .controls .row{
    width: 100%;
  }
  .controls .row .save-img{
    margin-left: 0px;
  }
}

#canvas_content {
  display: none;
}

.button-create {
  width: auto;
  margin: 0 auto 20px auto;
}

.drag_div {
  display: grid;
  justify-content: center;
  text-align: center;
  width: 150px;
  border: 1px solid #ccc;
  min-height: 150px;
  padding: inherit;
  border-radius: 20px;
  border-style: dashed;
}

.title_upload {
  width: 100px;
}